import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { AdminSideMenu, EssentialRoute } from "@enum/app_enum";
import { ReportService } from "@core/services/serviceIndex";
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-report-teacher',
  templateUrl: './report-teacher.component.html',
  styleUrls: ['./report-teacher.component.css']
})
export class ReportTeacherComponent implements OnInit,OnDestroy {
  public routeLists: any = {
    routeName: [AdminSideMenu.Report],
    routeLink: []
  };
  public reportLists: any = [];
  public page: any = 1;
  public limit: any = 10;
  public direction: String = 'down';
  public searchData: String;
  public isclassEmpty: boolean = false;
  public subscription: Subscription;

  constructor(private _service: ReportService, private _router: Router,) { }

  ngOnInit(): void {

    this.getReportList();
  }

  getReportList() {
    this.subscription = this._service.getUsersClasses(this.page, this.limit, this.direction, this.searchData)
      .subscribe((res) => {
        if (this.direction) {
          this.isclassEmpty = res.classes.length == 0;
          this.reportLists = this.reportLists.concat(res.classes);
        }
        else if (this.searchData) {
          this.reportLists = res.classes;
          this.page = 1;
        }
        else {
          this.reportLists = res.classes;
        }
        console.log(this.reportLists)
      })
  }

  onsearch(val: String) {
    this.searchData = val;
    this.reportLists = [];
    this.getReportList();
  }

  onScroll(event: any) {
    var element = event.target;
    if (!this.searchData) {
      if (element.scrollHeight - element.scrollTop === element.clientHeight) {
        if (!this.isclassEmpty) {
          this.direction = "down";
          this.page += 1;
          this.getReportList()
        }
      }
    }
  }

  goToStudents(id: String) {
    this._router.navigateByUrl("teacher/" + EssentialRoute.StudentByReport + "/" + id)
  }

  ngOnDestroy(){
    this.subscription.unsubscribe();
  }
}
